:root {
  --background: #F5F5F7;
  --foreground: #1C1C1E;
  --card: #FFFFFF;
  --card-foreground: #1C1C1E;
  --accent: #4A90E2;
  --accent-foreground: #FFFFFF;
  --accent-200: #89BFFF;
  --border: #C7C7CC;
  --muted: #EFF1F4;
  --muted-foreground: #5A5A5E;
  --ring: var(--accent);
  --radius: 14px;
  --shadow-sm: 0 1px 2px rgba(0,0,0,0.06);
  --shadow: 0 4px 12px rgba(0,0,0,0.08);
  --shadow-lg: 0 12px 24px rgba(0,0,0,0.12);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #1C1C1E;
    --foreground: #F5F5F7;
    --card: #121316;
    --card-foreground: #F5F5F7;
    --accent: #89BFFF;
    --accent-foreground: #0A0A0A;
    --accent-200: #4A90E2;
    --border: #3A3A3C;
    --muted: #2A2A2E;
    --muted-foreground: #D1D5DB;
    --ring: var(--accent);
  }
}
* {
  border-color: var(--border);
}
html, body {
  height: 100%;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-pretendard), "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
