  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
:focus-visible {
  outline: 2px solid var(--ring);
  outline-offset: 3px;
}
a {
  color: var(--accent);
  text-underline-offset: 3px;
  transition: color .2s ease, opacity .2s ease;
}
a:hover {
  color: var(--accent-200);
}
h1, h2, h3, h4 {
  letter-spacing: -0.01em;
  font-weight: 700;
}
h1 {
  font-size: clamp(2rem, 4vw, 2.75rem);
  line-height: 1.15;
}
h2 {
  font-size: clamp(1.5rem, 3vw, 2rem);
  line-height: 1.2;
}
h3 {
  font-size: clamp(1.25rem, 2.5vw, 1.5rem);
  line-height: 1.3;
}
h4 {
  font-size: 1.125rem;
  line-height: 1.35;
}
p, li {
  color: var(--foreground);
  @supports (color: color-mix(in lab, red, red)) {
    color: color-mix(in oklab, var(--foreground) 86%, transparent);
  }
}
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.625rem 1rem;
  border-radius: var(--radius);
  border: 1px solid transparent;
  background-color: var(--accent);
  color: var(--accent-foreground);
  font-weight: 600;
  line-height: 1;
  box-shadow: var(--shadow-sm);
  transition: background-color .2s ease, box-shadow .2s ease, transform .02s ease;
}
.btn:hover {
  background-color: var(--accent-200);
  box-shadow: var(--shadow);
}
.btn:active {
  transform: translateY(0.5px);
}
.btn:disabled {
  opacity: .6;
  cursor: not-allowed;
}
.btn--ghost {
  background: transparent;
  color: var(--foreground);
  border-color: var(--border);
}
.btn--ghost:hover {
  background: var(--foreground);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--foreground) 6%, transparent);
  }
}
.btn--outline {
  background: transparent;
  color: var(--accent);
  border-color: var(--accent);
}
.btn--outline:hover {
  background: var(--accent);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--accent) 12%, transparent);
  }
  color: var(--accent-foreground);
}
.card {
  background: var(--card);
  color: var(--card-foreground);
  border: 1px solid var(--border);
  border-radius: calc(var(--radius) + 2px);
  box-shadow: var(--shadow);
}
.card--p {
  padding: 1rem;
}
.card--p-lg {
  padding: 1.5rem;
}
.input, .select, .textarea {
  width: 100%;
  background: var(--card);
  color: var(--foreground);
  border: 1px solid var(--border);
  border-radius: calc(var(--radius) - 4px);
  padding: 0.625rem 0.75rem;
  transition: border-color .15s ease, box-shadow .15s ease;
}
.textarea {
  min-height: 120px;
  resize: vertical;
}
.input::placeholder, .textarea::placeholder {
  color: var(--muted-foreground);
}
.input:focus, .select:focus, .textarea:focus {
  border-color: var(--ring);
  box-shadow: 0 0 0 4px var(--ring);
  @supports (color: color-mix(in lab, red, red)) {
    box-shadow: 0 0 0 4px color-mix(in oklab, var(--ring) 25%, transparent);
  }
  outline: none;
}
.label {
  display: inline-block;
  margin-bottom: 0.375rem;
  font-weight: 600;
}
.help {
  margin-top: 0.375rem;
  font-size: 0.875rem;
  color: var(--muted-foreground);
}
.chip {
  display: inline-flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.25rem 0.5rem;
  border-radius: 9999px;
  background: var(--muted);
  color: var(--foreground);
  border: 1px solid var(--border);
  font-size: 0.8125rem;
  font-weight: 600;
}
.navbar {
  position: sticky;
  top: 0;
  z-index: 40;
  backdrop-filter: saturate(140%) blur(10px);
  background: var(--background);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--background) 82%, transparent);
  }
  border-bottom: 1px solid var(--border);
}
.navbar__inner {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0.75rem 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: space-between;
}
.navbar__link {
  padding: 0.5rem 0.75rem;
  border-radius: 10px;
  color: var(--foreground);
  transition: background-color .2s ease, color .2s ease;
}
.navbar__link[aria-current="page"], .navbar__link:hover {
  background: var(--foreground);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in oklab, var(--foreground) 6%, transparent);
  }
}
.container {
  max-width: 1120px;
  margin-inline: auto;
  padding-inline: clamp(1rem, 4vw, 2rem);
}
.hr {
  height: 1px;
  width: 100%;
  background: linear-gradient(90deg, transparent, var(--border), transparent);
}
.grid-projects {
  display: grid;
  grid-template-columns: repeat(1, minmax(0,1fr));
  gap: 1rem;
}
@media (min-width: 768px) {
  .grid-projects {
    grid-template-columns: repeat(2, minmax(0,1fr));
  }
}
@media (min-width: 1200px) {
  .grid-projects {
    grid-template-columns: repeat(3, minmax(0,1fr));
  }
}
.footer {
  border-top: 1px solid var(--border);
  color: var(--muted-foreground);
}
.kbd {
  border: 1px solid var(--border);
  background: var(--muted);
  border-radius: 6px;
  padding: 0.125rem 0.375rem;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.8125rem;
}
@property --tw-font-weight {
  syntax: "*";
  inherits: false;
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-font-weight: initial;
    }
  }
}
